.frame {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 700px;
  height: 450px;
  margin-top: -200px;
  margin-left: -350px;
  overflow: hidden;
  background: transparent;
  color: #E0E0E0;
  text-shadow: 2px 2px 2px black;
  font-family: 'Caveat Brush', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.circle {
  position: absolute;
  z-index: 10;
  height: 80px;
  width: 80px;
  top: 160px;
  left: 300px;
  background: #E0E0E0;
  border-radius: 50%;
  -webkit-animation: circle 1s ease-in-out;
          animation: circle 1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.line {
  position: absolute;
  z-index: 10;
  width: 150px;
  height: 4px;
  top: 198px;
  background: #E0E0E0;
  transform: scaleX(0); }
  .line.left {
    left: 50px;
    transform-origin: 100% 50%;
    -webkit-animation: lines 1s ease-in-out 0.8s, line-left 1s steps(1) 0.8s;
            animation: lines 1s ease-in-out 0.8s, line-left 1s steps(1) 0.8s; }
  .line.right {
    right: 50px;
    transform-origin: 0% 50%;
    -webkit-animation: lines 1s ease-in-out 0.8s, line-right 1s steps(1) 0.8s;
            animation: lines 1s ease-in-out 0.8s, line-right 1s steps(1) 0.8s; }

.bracket {
  position: absolute;
  z-index: 10;
  height: 140px;
  width: 4px;
  top: 130px;
  background: #E0E0E0;
  -webkit-animation: bracket .4s ease-out 1.7s;
          animation: bracket .4s ease-out 1.7s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; }
  .bracket:before, .bracket:after {
    position: absolute;
    display: block;
    content: '';
    width: 25px;
    height: 4px;
    background: #E0E0E0; }
  .bracket.left {
    left: 5px; }
    .bracket.left:before {
      top: 0;
      left: 0;
      transform-origin: 0% 50%;
      -webkit-animation: bracket-line .2s ease-out 2.1s;
              animation: bracket-line .2s ease-out 2.1s;
      -webkit-animation-fill-mode: both;
              animation-fill-mode: both; }
    .bracket.left:after {
      bottom: 0;
      left: 0;
      transform-origin: 0% 50%;
      -webkit-animation: bracket-line .2s ease-out 2.1s;
              animation: bracket-line .2s ease-out 2.1s;
      -webkit-animation-fill-mode: both;
              animation-fill-mode: both; }
  .bracket.right {
    right: 5px; }
    .bracket.right:before {
      top: 0;
      right: 0;
      transform-origin: 100% 50%;
      -webkit-animation: bracket-line .2s ease-out 2.1s;
              animation: bracket-line .2s ease-out 2.1s;
      -webkit-animation-fill-mode: both;
              animation-fill-mode: both; }
    .bracket.right:after {
      bottom: 0;
      right: 0;
      transform-origin: 100% 50%;
      -webkit-animation: bracket-line .2s ease-out 2.1s;
              animation: bracket-line .2s ease-out 2.1s;
      -webkit-animation-fill-mode: both;
              animation-fill-mode: both; }

.big {
  position: absolute;
  z-index: 5;
  top: 173px;
  width: 700px;
  text-align: center;
  font-weight: 800;
  font-size: 150px;
  color: #E0E0E0;
  line-height: 50px;
  text-transform: uppercase;
