.hide {
  position: absolute;
  z-index: 7;
  width: 700px;
  height: 80px;
  background: transparent;
  left: 0;
  -webkit-animation: reveal .4s ease-out 1.7s;
          animation: reveal .4s ease-out 1.7s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both; }
  .hide.top {
    bottom: 40%;
    transform-origin: 30% 0%; }
  .hide.bottom {
    top: 30%;
    transform-origin: 30% 100%; }

.small {
  position: absolute;
  z-index: 10;
