  .small.top {
    top: 90px;
    -webkit-animation: small-top .8s ease-out 3s;
            animation: small-top .8s ease-out 3s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both; }
  .small.bottom {
    bottom: 140px;
    -webkit-animation: small-bottom .8s ease-out 3s;
            animation: small-bottom .8s ease-out 3s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both; }

